import { AppBar, Toolbar, Typography } from "@mui/material";
import { useNavigate } from "react-router-dom";

function NavBar() {
    const navigate = useNavigate()

    function logout() {
        sessionStorage.clear()
        navigate('/sign-in')
    }

    function navigateTo(e, url) {
        navigate(url)
    }

    return (
        <AppBar position="static">
            <Toolbar>
                <Typography 
                    variant="h5" 
                    onClick={ (e) => navigateTo(e, '/habits') }
                    sx={{ 
                        mr: 6, 
                        color: 'white', 
                        textDecoration: 'none',
                        cursor: 'pointer'
                    }}>Habits
                </Typography>
                <Typography 
                    variant="h5" 
                    onClick={ (e) => navigateTo(e, '/create-habit') }
                    sx={{ 
                        flexGrow: 3, 
                        color: 'white', 
                        textDecoration: 'none',
                        cursor: 'pointer'
                    }}>Create Habit
                </Typography>
                <Typography variant="h5" onClick={ logout }>Log Out</Typography>
            </Toolbar>
        </AppBar>
    )
}

export default NavBar