import { Container, Typography, Paper, Grid } from "@mui/material"
import { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import { CssVarsProvider, CircularProgress } from "@mui/joy"

function calculateProgress(progress) {
    let done = 0

    for (let i = 0; i < progress.length; i++) {
        progress[i] && done++
    }

    return done / progress.length * 100
}

function HabitItem(props) {
    const navigate = useNavigate()

    function handleNav(e, id) {
        navigate(`/habit/${id}`)
    }

    return (
        <Grid 
            item 
            xs={6}
            sm={3}
            md={2}
            lg={2}
            margin={2}
            onClick={ (e) => handleNav(e, props.id) }
            sx={{ 
                textDecoration: 'none', 
                ':hover': { transform: 'scale(1.05)' } 
            }}>
            <Paper 
                square 
                elevation={3} 
                sx={{ 
                    'height': 150, 
                    'width': { xs: 150, sm: 120, md: 120, lg: 150 }, 
                    p: 2 
                }}>
                <CssVarsProvider>
                    <CircularProgress 
                        determinate 
                        value={calculateProgress(props.progress)} size='lg'
                        sx={{
                            "--CircularProgress-size": "100px"
                        }}>
                        { Math.round(calculateProgress(props.progress)) + '%' }
                    </CircularProgress>
                </CssVarsProvider>
                <Typography variant="h5" sx={{ 
                    mt: 2, 
                    textOverflow: 'ellipsis', 
                    whiteSpace: 'nowrap', 
                    overflow: 'hidden' 
                }}>{ props.habit }</Typography>
            </Paper>
        </Grid>
    )
}

function Habits() {
    const [username, setUsername] = useState('')
    let [habits, setHabits] = useState([])
    const navigate = useNavigate()

    useEffect(() => {
        !sessionStorage.getItem('token') && navigate('/sign-in') 

        async function fetchData() {    
            let data = await fetch('/habits', {
                headers: {
                    'authorization': sessionStorage.getItem('token')
                }
            })
            data = await data.json()

            setUsername(data.username)
            setHabits(data.habits)
        }
        fetchData()
    }, [habits])

    return (
        <Container>
            <Typography variant="h3" m={3}>{ username }</Typography>
            <Grid 
                container 
                spacing={3}
                justifyContent='center'
                sx={{ width: '100%' }}>
                { habits?.map(habit => 
                    <HabitItem 
                        id={ habit._id } 
                        key={habit._id} 
                        habit={ habit.habit } 
                        progress={ habit.progress } 
                        />) }
            </Grid>
        </Container>
    )
}

export default Habits