import { Box, Button, Container, TextField, Typography, Avatar, Link, Alert, IconButton } from '@mui/material'
import { LockOutlined } from '@mui/icons-material'
import { useNavigate } from 'react-router-dom'
import { useState } from 'react'

function SignUp() {
    const [error, setError] = useState(false)
    const navigate = useNavigate()

    function navToSignIn() {
        navigate('/sign-in')
    }

    const handleSubmit = async (event) => {
        event.preventDefault()
        const data = new FormData(event.currentTarget)

        if (data.get('username') === "" || data.get('password') === "") {
            setError(true)
            return null
        }

        let response = await fetch('/sign-up', {
            method: 'POST',
            headers: {
                'Content-type': 'application/json'  
            },
            body: JSON.stringify({
                username: data.get('username'),
                password: data.get('password'),
            }),
        })

        try {
            response = await response.json()

            if (response.token === "") {
                setError(true)
                return null
            }

            sessionStorage.setItem('token', response.token)
            navigate('/habits')
        } catch {
            console.log('user already exists')
        }
    }

    return (
        <Container component='main' maxWidth='xs'>
            { 
                error && <Alert severity='info' onClose={ () => setError(false) }>
                    Invalid username or password
                </Alert> 
            }
            <Box sx={{ 
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                }} >  
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <LockOutlined />
                </Avatar>
                <Typography component="h1" variant="h5">Sign up</Typography>
                <Box component='form' onSubmit={ handleSubmit } noValidate sx={{ mt: 1 }}>
                    <TextField 
                        margin='normal'
                        fullWidth
                        id="username" 
                        label="Username"
                        name='username'
                        />
                    <TextField 
                        margin='normal'
                        fullWidth
                        id="password" 
                        label="Password"
                        name='password'
                        type='password'
                        />
                    <Button 
                        variant='contained' 
                        type='submit'
                        fullWidth
                        sx={{ mt: 3, mb: 2 }}
                        >Sign up</Button>
                    <Link onClick={ navToSignIn } variant='body2'>
                        { "Already have an account? Sign in" }
                    </Link>    
                </Box>
            </Box>
        </Container>  
    )
}

export default SignUp