import { Box, Button, Container, TextField, Typography, Avatar, Link, Alert } from '@mui/material'
import { LockOutlined } from '@mui/icons-material'
import { useNavigate } from 'react-router-dom'
import { useState } from 'react'

function SignIn() {
    const [error, setError] = useState(false)
    const navigate = useNavigate()

    function navTosignUp() {
        navigate('/sign-up')
    }

    const handleSubmit = async (event) => {
        event.preventDefault()
        const data = new FormData(event.currentTarget)
        
        if (data.get('username') === "" || data.get('password') === "") {
            setError(true)
            return null
        }

        let token = await fetch('/sign-in', {
            method: 'POST',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify({
                username: data.get('username'),
                password: data.get('password'),
            }),
        })
        token = await token.json()
        if (token.token === "") {
            setError(true)
            return null
        }
        
        sessionStorage.setItem('token', token.token)
        navigate('/habits')
    }

    return (
        <Container component='main' maxWidth='xs'>
            { error && <Alert severity='info' onClose={ () => setError(false) }>Invalid username or password</Alert> }
            <Box sx={{ 
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                }} >  
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <LockOutlined />
                </Avatar>
                <Typography component="h1" variant="h5">Sign in</Typography>
                <Box component='form' onSubmit={ handleSubmit } noValidate sx={{ mt: 1 }}>
                    <TextField 
                        margin='normal'
                        fullWidth
                        id="username" 
                        label="Username"
                        name='username'
                        />
                    <TextField 
                        margin='normal'
                        fullWidth
                        id="password" 
                        label="Password"
                        name='password'
                        type='password'
                        />
                    <Button 
                        variant='contained' 
                        type='submit'
                        fullWidth
                        sx={{ mt: 3, mb: 2 }}
                        >Sign In</Button>
                    <Link onClick={ navTosignUp } variant='body2'>
                        { "Don't have an account? Sign Up" }
                    </Link>    
                </Box>
            </Box>
        </Container>        
    )
}

export default SignIn