import { Box, Button, Container, Paper, TextField, Tooltip, Typography } from "@mui/material"
import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"

function Habit() {
    const { id } = useParams()
    const [habit, setHabit] = useState('')
    const [description, setDescription] = useState('')
    const [habitDays, setHabitDays] = useState(0)
    const [progress, setProgress] = useState([])
    const [streak, setStreak] = useState(0)
    const navigate = useNavigate()

    function handleProgressSubmit(event) {
        event.preventDefault()
        const data = new FormData(event.currentTarget)
        
        fetch(`/change-progress/${id}`, {
            method: 'POST', 
            headers: {
                'Content-type': 'application/json',
                'authorization': sessionStorage.getItem('token')
            },
            body: JSON.stringify({
                day: data.get('day'),
            })
        })
    }

    function handleDeleteSubmit(event) {
        event.preventDefault()

        fetch(`/delete-habit/${id}`, {
            method: 'POST',
            headers: {
                'Content-type': 'application/json',
                'authorization': sessionStorage.getItem('token')
            },
        })

        navigate('/habits')
    }

    function findLongestStreak(progress) {
        let ans = 1, temp = 1;
 
        for (let i = 1; i < progress.length; i++) {
            if (progress[i] == progress[i - 1] && progress[i] == true) {
                ++temp;
            }
            else {
                ans = Math.max(ans, temp);
                temp = 1;
            }
        }
        ans = Math.max(ans, temp);
    
        return ans;
    }
    
    useEffect(() => {
        !sessionStorage.getItem('token') && navigate('/sign-in')
        
        async function fetchData() {
            let data = await fetch(`/habit/${id}`, {
                method: 'POST',
                headers: {
                    'Content-type': 'application/json',
                    'authorization': sessionStorage.getItem('token')
                }
            })
            data = await data.json()
            setHabit(data.habit)
            setDescription(data.description)
            setHabitDays(data.days)
            setProgress(data.progress)
            setStreak(findLongestStreak(data.progress))
        }
        fetchData()
    }, [progress])

    return (
        <Container maxWidth='sm'>
            <Paper sx={{ p: 2, m: 2 }} square elevation={5}>
                <Typography variant="h2">{ habit }</Typography>
                <Typography variant="h6">description: { description }</Typography>
                <Typography variant="h6">days: { habitDays }</Typography>
                <Typography variant="h6" sx={{ width: '100%', overflowWrap: 'break-word', cursor: 'default' }}>
                    progess: { 
                        progress?.map((item, index) => 
                            <Tooltip title={ index+1 }>
                                <Typography variant="p">{ item ? '' : '' }</Typography>
                            </Tooltip> ) 
                    }
                </Typography>
                { streak > 1 && <Typography variant="h6">longest streak: { streak } days</Typography> }

                <Box component='form' onSubmit={ handleProgressSubmit } noValidate>
                    <TextField
                        id="day" 
                        type='number' 
                        name="day"
                        label='Day'
                        margin="normal"
                        />
                    <Button variant="contained" type="submit" sx={{ m: 2 }}>Done</Button>
                </Box>

                <Button variant="contained" onClick={ handleDeleteSubmit }>Delete</Button>
            </Paper>
        </Container>
    )
}

export default Habit